@import url(https://cdn.jsdelivr.net/npm/reset-css@5.0.1/reset.min.css);
.authLogo {
  margin-bottom: 30px;
  color: #7ad1c0;
  font-size: 2rem;
}

.each_comment{
    display:flex;
    padding:5px 0;
    text-align:left;
    border-bottom:1px solid #efefef;
    font-size:0.8rem;
}

.comment_username{
    font-weight:700;
    padding-right:10px;
}
.comment_input{
    text-indent: 10px;
    line-height:20px;
    border-radius: 5px;
    background-color:#efefef;
}

.comments_box{
    width:100%;
    margin-top:15px;
}

.add_comment{
    display:flex;
    justify-content: space-between;
}

.comment_input{
    width:87%;
    text-align:left;
}
.comment_submit{
    width:11%;
    background-color:rgba(122, 209, 192, 0.7);
    border-radius:7px;
    font-size:0.7rem;
    text-align:center;
    cursor:pointer;
}

.comments_list{
    padding-top:10px;
}
.nweet_user {
  display: flex;
  align-items: center;
  padding-bottom: 10px;
}

.user_photo {
  width: 25px;
  height: 25px;
  margin-right: 3px;
  border: 1px solid #fff;
  border-radius: 50%;
  overflow: hidden;
  display: flex;
  justify-content: center;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
}
.user_photo > img {
  width: 100%;
  height: 100%;
}
.user_name {
  padding-left: 5px;
  color: #444;
  font-size: 0.9rem;
  font-weight: 700;
}

.nweet_content{
    padding:10px 0;
}

.date {
  position: absolute;
  right: 20px;
  top: 20px;
  color: #777;
  font-size: 0.4rem;
}

.like_and_comment {
  margin-top: 10px;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
  text-align: right;
}

.like {
  padding-right: 5px;
  color: #da5a6f;
}

.like_number {
  padding-left: 2px;
  color: #333;
}

.nweet_actions {
  padding-top: 20px;
  font-size: 0.6rem;
  text-align: right;
  color: #555;
}

.actions_icon {
  padding-left: 5px;
  cursor: pointer;
}

.delete_icon,
.edit_icon {
  padding-right: 2px;
}

.formBtn_icon {
  display: flex;
  align-items: center;
  width: 100%;
  height: 30px;
  margin-top: 10px;
}

.formBtn_text {
  height: 100%;
  line-height: 30px;
  flex-basis: 85%;
  text-align: center;
  border-top-left-radius: 20px;
  border-bottom-left-radius: 20px;
  background-color: rgba(122, 209, 192, 0.5);
}

.formBtn_text_cancel {
  background-color: rgba(255, 178, 4, 0.5);
}

.editing_icon {
  height: 100%;
  line-height: 30px;
  text-align: center;
  flex-basis: 15%;
  border-top-right-radius: 20px;
  border-bottom-right-radius: 20px;
  background-color: rgba(122, 209, 192, 1);
  color:rgba(255,255,255,0.8)
}

.editing_icon.cancel {
  background-color: rgba(255, 178, 4, 1);
}

/* dark mode  */
.nweet.dark {
  background-color: #1f1e1e;
  color: #efefef;
}

.nweet.dark .user_name {
  color: #aaa;
}

.nweet.dark .like_number {
  color: #efefef;
}

.nweet.dark .comment_input,
.nweet.dark .formInput {
  background-color: #555;
}

.nweet.dark .formInput {
  color: #efefef;
}

.nweet_user.account{
    width: 100%;
}
.factoryInput_container{
    position:relative;
    margin-bottom:10px;
}
.nweets{
    margin-top:30px;
} 
.navigation {
  display: flex;
  justify-content: center;
  margin-top: 50px;
}

.home {
  margin-right: 10px;
}

.profile {
  margin-left: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 12px;
  text-align: center;
}

.homeText,
.profileText {
  display: block;
  margin-top: 10px;
  font-size:0.7rem;
}

/* icon */
.hippo,
.user {
  color: #7ad1c0;
  font-size: 1.5rem;
}

.profileForm {
  position: relative;
}

.profile_photo {
  width: 120px;
  height: 120px;
  margin: 0 auto 15px;
  border-radius: 50%;
  overflow: hidden;
  display: flex;
  justify-content: center;
  position:relative;
  box-shadow: 0 0 10px rgba(62,62,62,0.45);
}

.profile_photo > img {
  width: 100%;
}

.formBtn {
  /* margin-top: 10px; */
}

.post{
    padding:20px;
    border-top:2px solid #efefef;
    position:relative;
    display:flex;
    flex-direction: column;
}

.post > .post_img{
   padding:10px 0;
}
.account_wrap{
    width:50%;
}
.router_loggedIn{
    max-width: 890px;
    width:100%;
    margin:0 auto;
    margin-top:80px;
    display:flex;
    justify-content: center;
}

.mode{
    padding:5px;
    position:absolute;
    right:15px;
    top:15px;
    border:none;
    outline:none;
    border-radius: 5px;
    font-size:0.6rem;
    background-color:#333;
    color:#fff;
    transition:all 0.3s ease;
    cursor:pointer;
    box-shadow:0 0 5px rgba(0,0,0,0.7)
}

/* dark mode */
.mode.dark{
    background-color:#efefef;
    color:#000;
    box-shadow:0 0 10px rgba(255,255,255,0.7)
}
* {
  box-sizing: border-box;
}

input {
  all: unset;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-size: 14px;
}

button {
  background-color: white;
  color: black;
}

a {
  text-decoration: none;
  color: inherit;
}

form {
  width: 100%;
}

.nweet-input::-webkit-input-placeholder {
  color: white;
  opacity: 0.9;
}

.nweet-input:-ms-input-placeholder {
  color: white;
  opacity: 0.9;
}

.nweet-input::-ms-input-placeholder {
  color: white;
  opacity: 0.9;
}

.nweet-input::placeholder {
  color: white;
  opacity: 0.9;
}

.container {
  width: 100%;
  max-width: 320px;
  display: flex;
  flex-direction: column;
}

.formInput {
  width: 100%;
  padding: 10px 20px;
  border-radius: 20px;
  border: 1px solid black;
  text-align: center;
  background-color: white;
  color: black;
}

.formBtn {
  width: 100%;
  padding: 7px 20px;
  margin-top: 10px;
  text-align: center;
  color: white;
  border-radius: 20px;
  background-color: rgba(122, 209, 192, 0.7);
  border-radius: 20px;
  cursor: pointer;
}

.cancelBtn {
  cursor: pointer;
  background-color: rgba(4, 121, 255, 0.7);
}

/* Profile Page */

.profileForm {
  border-bottom: 1px solid rgba(255, 255, 255, 0.9);
  padding-bottom: 30px;
  width: 100%;
  display: flex;
  flex-direction: column;
}

.logOut {
  margin-top: 50px;
}

/* Nwitter Factory */

.factoryForm {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}

.factoryInput__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  position: relative;
  margin-bottom: 20px;
  width: 100%;
}

.factoryInput__input {
  flex-grow: 1;
  height: 40px;
  padding: 0px 30px;
  border: 1px solid #7ad1c0;
  border-radius: 20px;
  font-weight: 500;
  font-size: 12px;
}

.factoryInput__arrow {
  position: absolute;
  right: 0;
  background-color: #7ad1c0;
  height: 40px;
  width: 40px;
  padding: 10px 0px;
  text-align: center;
  border-radius: 20px;
  color: white;
}

.factoryInput__label {
  color: #7ad1c0;
  cursor: pointer;
}

.factoryInput__label span {
  margin-right: 10px;
  font-size: 12px;
}

.factoryForm__attachment {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.factoryForm__attachment img {
  width: 150px;
  padding-bottom: 5px;
}

.factoryForm__clear {
  color: #7ad1c0;
  cursor: pointer;
  text-align: center;
}

.factoryForm__clear span {
  margin-right: 10px;
  font-size: 12px;
}

/* Nweet */

.nweetEdit .formBtn {
  cursor: pointer;
  /* margin-top: 15px;
  margin-bottom: 5px; */
  position: relative;
}

.nweet {
  margin-bottom: 35px;
  background-color: white;
  width: 100%;
  max-width: 320px;
  padding: 20px;
  border-radius: 10px;
  position: relative;
  display: flex;
  flex-direction: column;
  color: rgba(0, 0, 0, 0.8);
  box-shadow: 0 0 10px rgba(62, 62, 62, 0.25);
}

.nweet h4 {
  font-size: 14px;
}

.nweet img {
  /* right: -10px;
  top: 20px;
  position: absolute;
  border-radius: 50%;
  width: 50px;
  height: 50px; */
  /* margin-top: 10px; */
}

.nweet__actions {
  position: absolute;
  right: 10px;
  top: 10px;
}

.nweet__actions span {
  cursor: pointer;
}

.nweet__actions span:first-child {
  margin-right: 10px;
}

/* Auth */

.authContainer {
  display: flex;
  flex-direction: column;
  height: 100vh;
  justify-content: center;
  align-items: center;
}

.authBtns {
  display: flex;
  justify-content: space-between;
  width: 100%;
  max-width: 320px;
}

.authBtn {
  width:49%;
  cursor: pointer;
  border-radius: 20px;
  border: none;
  padding: 10px 0px;
  font-size: 12px;
  text-align: center;
  background: #efefef;
  cursor: pointer;
}

.authBtn_text{
  padding-left:5px;
}

.fa-google{
  font-size:14px;
  color:rgba(40, 121, 226, 0.74);
}
.fa-github{
  font-size:14px;
  color:rgba(0, 0, 0, 0.74);
}

/* AuthForm */

.authInput {
  max-width: 320px;
  width: 100%;
  padding: 10px;
  border-radius: 30px;
  background-color: #dad6d6;
  margin-bottom: 10px;
  font-size: 12px;
  color: black;
}

.authSubmit {
  text-align: center;
  background: #7ad1c0;
  color: white;
  margin-top: 10;
  cursor: pointer;
  transition: all 0.2s ease;
}

.authSubmit:hover {
  opacity: 0.8;
}

.authError {
  color: tomato;
  text-align: center;
  font-weight: 500;
  font-size: 12px;
}

.authSwitch {
  color: #7ad1c0;
  cursor: pointer;
  margin-top: 10px;
  margin-bottom: 50px;
  display: block;
  font-size: 12px;
  text-decoration: underline;
}



/* dark mode */
body.dark {
  background-color: #000;
  color: #efefef;
}

.authInput.dark{
  background-color:#1f1e1e;
  color:#efefef;
}

.authBtn.dark{
  background-color:#555;
  color:#efefef;
}

.authBtn.dark > .fa-google{
  color:rgba(40, 121, 226, 0.74);
}

.image_file {
  width: 120px;
  height: 120px;
  border-radius: 50%;
  overflow: hidden;
  position: absolute;
  left: 50%;
  top: 0;
  transform: translateX(-50%);
}
.file_input {
  display: none;
}

.file_button {
  display: block;
  width: 100%;
  height: 100%;
  border: none;
  outline: none;
  font-size: 0.8rem;
  cursor: pointer;
  background-color:rgba(0,0,0,0.3);
  color:#fff;
  opacity:0;
  transition:all 0.3s ease;
}

.file_button:hover{
    opacity:1;
}

