@import "https://cdn.jsdelivr.net/npm/reset-css@5.0.1/reset.min.css";


* {
  box-sizing: border-box;
}

input {
  all: unset;
  appearance: none;
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-size: 14px;
}

button {
  background-color: white;
  color: black;
}

a {
  text-decoration: none;
  color: inherit;
}

form {
  width: 100%;
}

.nweet-input::placeholder {
  color: white;
  opacity: 0.9;
}

.container {
  width: 100%;
  max-width: 320px;
  display: flex;
  flex-direction: column;
}

.formInput {
  width: 100%;
  padding: 10px 20px;
  border-radius: 20px;
  border: 1px solid black;
  text-align: center;
  background-color: white;
  color: black;
}

.formBtn {
  width: 100%;
  padding: 7px 20px;
  margin-top: 10px;
  text-align: center;
  color: white;
  border-radius: 20px;
  background-color: rgba(122, 209, 192, 0.7);
  border-radius: 20px;
  cursor: pointer;
}

.cancelBtn {
  cursor: pointer;
  background-color: rgba(4, 121, 255, 0.7);
}

/* Profile Page */

.profileForm {
  border-bottom: 1px solid rgba(255, 255, 255, 0.9);
  padding-bottom: 30px;
  width: 100%;
  display: flex;
  flex-direction: column;
}

.logOut {
  margin-top: 50px;
}

/* Nwitter Factory */

.factoryForm {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}

.factoryInput__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  position: relative;
  margin-bottom: 20px;
  width: 100%;
}

.factoryInput__input {
  flex-grow: 1;
  height: 40px;
  padding: 0px 30px;
  border: 1px solid #7ad1c0;
  border-radius: 20px;
  font-weight: 500;
  font-size: 12px;
}

.factoryInput__arrow {
  position: absolute;
  right: 0;
  background-color: #7ad1c0;
  height: 40px;
  width: 40px;
  padding: 10px 0px;
  text-align: center;
  border-radius: 20px;
  color: white;
}

.factoryInput__label {
  color: #7ad1c0;
  cursor: pointer;
}

.factoryInput__label span {
  margin-right: 10px;
  font-size: 12px;
}

.factoryForm__attachment {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.factoryForm__attachment img {
  width: 150px;
  padding-bottom: 5px;
}

.factoryForm__clear {
  color: #7ad1c0;
  cursor: pointer;
  text-align: center;
}

.factoryForm__clear span {
  margin-right: 10px;
  font-size: 12px;
}

/* Nweet */

.nweetEdit .formBtn {
  cursor: pointer;
  /* margin-top: 15px;
  margin-bottom: 5px; */
  position: relative;
}

.nweet {
  margin-bottom: 35px;
  background-color: white;
  width: 100%;
  max-width: 320px;
  padding: 20px;
  border-radius: 10px;
  position: relative;
  display: flex;
  flex-direction: column;
  color: rgba(0, 0, 0, 0.8);
  box-shadow: 0 0 10px rgba(62, 62, 62, 0.25);
}

.nweet h4 {
  font-size: 14px;
}

.nweet img {
  /* right: -10px;
  top: 20px;
  position: absolute;
  border-radius: 50%;
  width: 50px;
  height: 50px; */
  /* margin-top: 10px; */
}

.nweet__actions {
  position: absolute;
  right: 10px;
  top: 10px;
}

.nweet__actions span {
  cursor: pointer;
}

.nweet__actions span:first-child {
  margin-right: 10px;
}

/* Auth */

.authContainer {
  display: flex;
  flex-direction: column;
  height: 100vh;
  justify-content: center;
  align-items: center;
}

.authBtns {
  display: flex;
  justify-content: space-between;
  width: 100%;
  max-width: 320px;
}

.authBtn {
  width:49%;
  cursor: pointer;
  border-radius: 20px;
  border: none;
  padding: 10px 0px;
  font-size: 12px;
  text-align: center;
  background: #efefef;
  cursor: pointer;
}

.authBtn_text{
  padding-left:5px;
}

.fa-google{
  font-size:14px;
  color:rgba(40, 121, 226, 0.74);
}
.fa-github{
  font-size:14px;
  color:rgba(0, 0, 0, 0.74);
}

/* AuthForm */

.authInput {
  max-width: 320px;
  width: 100%;
  padding: 10px;
  border-radius: 30px;
  background-color: #dad6d6;
  margin-bottom: 10px;
  font-size: 12px;
  color: black;
}

.authSubmit {
  text-align: center;
  background: #7ad1c0;
  color: white;
  margin-top: 10;
  cursor: pointer;
  transition: all 0.2s ease;
}

.authSubmit:hover {
  opacity: 0.8;
}

.authError {
  color: tomato;
  text-align: center;
  font-weight: 500;
  font-size: 12px;
}

.authSwitch {
  color: #7ad1c0;
  cursor: pointer;
  margin-top: 10px;
  margin-bottom: 50px;
  display: block;
  font-size: 12px;
  text-decoration: underline;
}



/* dark mode */
body.dark {
  background-color: #000;
  color: #efefef;
}

.authInput.dark{
  background-color:#1f1e1e;
  color:#efefef;
}

.authBtn.dark{
  background-color:#555;
  color:#efefef;
}

.authBtn.dark > .fa-google{
  color:rgba(40, 121, 226, 0.74);
}
